.vue"],"names":[],"mappings":";AAqMA;IACA,UAAA;IACA,QAAA;IACA,kBAAA;IACA,WAAA;IACA,YAAA;AACA;AAEA;IACA,cAAA;IACA,iBAAA;IACA,mBAAA;IACA,wBAAA;IACA,WAAA;IACA,gBAAA;AACA;AAEA;IACA,gBAAA;AACA;AAEA;IACA,gBAAA;AACA;AAEA;IACA,yBAAA;AACA;AAEA;IACA,yBAAA;IACA,cAAA;AACA;AAEA;IACA,mBAAA;IACA,WAAA;IACA,mBAAA;IACA,qBAAA;IACA,yBAAA;IACA,uDAAA;IACA,oDAAA;IACA,+CAAA;AACA;AAEA;IACA,QAAA;IACA,SAAA;IACA,kBAAA;IACA,WAAA;IACA,yBAAA;IACA,UAAA;AACA;AAEA;IACA,kBAAA;AACA;AAEA;IACA,2BAAA;IACA,yCAAA;IACA,0CAAA;IACA,2CAAA;IACA,YAAA;IACA,qBAAA;IACA,aAAA;IACA,gBAAA;AACA;AAEA;IACA,eAAA;AACA;AAEA;IACA,2BAAA;IACA,yCAAA;IACA,0CAAA;IACA,wCAAA;IACA,SAAA;IACA,qBAAA;IACA,aAAA;IACA,gBAAA;AACA;AAEA;IACA,gBAAA;AACA;AAEA;IACA,2BAAA;IACA,yCAAA;IACA,wCAAA;IACA,2CAAA;IACA,UAAA;IACA,oBAAA;IACA,cAAA;IACA,eAAA;AACA;AAEA;IACA,iBAAA;AACA;AAEA;IACA,2BAAA;IACA,wCAAA;IACA,0CAAA;IACA,2CAAA;IACA,WAAA;IACA,oBAAA;IACA,cAAA;IACA,eAAA;AACA;AAEA;IACA,mBAAA;IACA,WAAA;IACA,aAAA;IACA,kBAAA;IACA,uCAAA;AACA;AAEA;IACA,qBAAA;AACA;AAEA;IACA,kBAAA;IACA,UAAA;IACA,2CAAA;AACA;AAEA;IACA,mBAAA;IACA,UAAA;IACA,yBAAA;AACA;AAEA;IACA,oBAAA;IACA,qBAAA;AACA;AAEA;IACA,YAAA;IACA,iBAAA;IACA,eAAA;AACA;AAEA;IACA,oCAAA;IACA,yBAAA;IACA,mCAAA;AACA;AAEA;AACA;QACA,uBAAA;AACA;AAEA;QACA,yBAAA;AACA;AACA;AAEA;IACA,aAAA;AACA","sourcesContent":["<template>\n    <div id=\"search-template-pattern\">\n        <div class=\"row\">\n            <div class=\"col-sm-12 content\">\n                <div class=\"template-wrapper\">\n                    <span class=\"template-title\">{{searchTemplate.title}}</span>\n                    <img src=\"images/info32.png\" width=\"16\" height=\"16\" style=\"margin-bottom: 2px;\" alt=\"\"\n                         v-tooltip.right=\"{\n                             content: searchTemplate.season === -1 ? 'Show Exception' : `Season ${searchTemplate.season} Exception`\n                         }\">\n                    <img\n                        v-if=\"!searchTemplate.default\"\n                        class=\"template-remove\" src=\"images/no16.png\"\n                        @click=\"$emit('remove', searchTemplate)\"\n                    >\n\n                    <div\n                        class=\"tooltip-wrapper-pattern\"\n                        v-tooltip.right=\"{ content: tooltipContent }\"\n                    >\n                        <div class=\"template-body\">\n                            <input\n                                :class=\"{ invalid: !validated }\"\n                                type=\"text\"\n                                name=\"search_pattern\"\n                                disabled=\"disabled\"\n                                v-model=\"searchTemplate.template\"\n                                class=\"form-control-inline-max input-sm max-input350 search-pattern\"\n                            >\n\n                            <input type=\"checkbox\" v-model=\"searchTemplate.enabled\">\n                            <i\n                                class=\"show-template glyphicon\"\n                                :class=\"`glyphicon-eye-${showExample ? 'close' : 'open'}`\"\n                                @click=\"showExample = !showExample\"\n                                title=\"Show template example\"\n                            />\n\n                        </div>\n                    </div>\n\n                </div>\n\n                <span\n                    :class=\"{ invalid: !validated }\"\n                    class=\"template-example\"\n                    v-if=\"showExample\"\n                    name=\"search_pattern\"\n                >Example: {{ searchTemplateExample }}\n                </span>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport { mapState } from 'vuex';\nimport formatDate from 'date-fns/format';\nimport { VTooltip } from 'v-tooltip';\nimport debounce from 'lodash/debounce';\n\nexport default {\n    name: 'search-template-pattern',\n    directives: {\n        tooltip: VTooltip\n    },\n    props: {\n        /**\n         * Provide the custom naming type. -Like sports, anime, air by date- description.\n         * If none provided we asume this is the default episode naming component.\n         * And that means there will be no checkbox available to enable/disable it.\n         */\n        format: {\n            type: String,\n            default: ''\n        },\n        /**\n         * Template object with properties for:\n         * - title: title.\n         * - template: Pattern consisting out of title + added characters and pattern substitutions.\n         * - season: \"-1\" for show title/pattern or the season number for season title/exceptions.\n         * - default: Is this a default pattern? yes or no.\n         * - enabled: enable/disable the template.\n         * - indexer: indexer nr.\n         * - seriesId: indexers series id.\n         */\n        template: {\n            type: Object\n        },\n        season: {\n            type: Boolean,\n            default: false\n        },\n        animeType: {\n            type: Number,\n            default: 3\n        }\n    },\n    data() {\n        return {\n            showFormat: null,\n            searchTemplate: '',\n            searchTemplateExample: '',\n            showExample: false,\n            validated: true,\n            seasonPattern: false\n        };\n    },\n    computed: {\n        ...mapState({\n            client: state => state.auth.client\n        }),\n        tooltipContent() {\n            const { searchTemplate } = this;\n            return searchTemplate.default ?\n                \"This template has been generated based on a scene exception / title. It's a default template and cannot be modified. It can only be enabled/disabled.\" :\n                'You can modify this template.';\n        }\n    },\n    methods: {\n        getDateFormat(format) {\n            return formatDate(new Date(), format);\n        },\n        async testNaming() {\n            const { animeType, searchTemplate, showFormat } = this;\n            console.debug(`Test pattern ${searchTemplate.template}`);\n\n            let params = {\n                pattern: searchTemplate.template\n            };\n\n            const formatMap = new Map([\n                // eslint-disable-next-line camelcase\n                ['anime', { anime_type: animeType }],\n                ['sports', { sports: true }],\n                ['airByDate', { abd: true }]\n            ]);\n\n            if (showFormat !== '') {\n                params = { ...params, ...formatMap.get(showFormat) };\n            }\n\n            let response = '';\n\n            try {\n                response = await this.client.apiRoute.get(\n                    'config/postProcessing/testNaming',\n                    {\n                        params,\n                        timeout: 20000\n                    }\n                );\n                this.searchTemplateExample = response.data;\n            } catch (error) {\n                console.warn(error);\n            }\n            return response;\n        },\n        updateExample() {\n            const { debouncedTestNaming } = this;\n\n            // Update single\n            debouncedTestNaming();\n        },\n        update() {\n            this.$nextTick(() => {\n                this.$emit('change', {\n                    template: this.searchTemplate,\n                    format: this.showFormat\n                });\n            });\n        }\n    },\n    mounted() {\n        const { format, season, template } = this;\n        this.searchTemplate = template;\n        this.showFormat = format;\n        this.seasonPattern = season;\n\n        // Update the pattern example\n        this.updateExample();\n    },\n    created() {\n        this.debouncedTestNaming = debounce(this.testNaming, 500);\n    },\n    watch: {\n        template(newTemplate) {\n            this.searchTemplate = newTemplate;\n            this.updateExample();\n        },\n        animeType() {\n            this.updateExample();\n        }\n    }\n};\n</script>\n<style scoped>\n.show-template {\n    left: -8px;\n    top: 4px;\n    position: absolute;\n    z-index: 10;\n    opacity: 0.6;\n}\n\n.template-example {\n    display: block;\n    line-height: 20px;\n    margin-bottom: 15px;\n    padding: 2px 5px 2px 2px;\n    clear: left;\n    max-width: 338px;\n}\n\n.template-wrapper {\n    max-width: 350px;\n}\n\n.tooltip-wrapper-pattern {\n    max-width: 350px;\n}\n\n.invalid {\n    background-color: #ff5b5b;\n}\n\n.tooltip {\n    display: block !important;\n    z-index: 10000;\n}\n\n.tooltip .tooltip-inner {\n    background: #ffef93;\n    color: #555;\n    border-radius: 16px;\n    padding: 5px 10px 4px;\n    border: 1px solid #f1d031;\n    -webkit-box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\n    -moz-box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\n    box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\n}\n\n.tooltip .tooltip-arrow {\n    width: 0;\n    height: 0;\n    position: absolute;\n    margin: 5px;\n    border: 1px solid #ffef93;\n    z-index: 1;\n}\n\n.tooltip[x-placement^='top'] {\n    margin-bottom: 5px;\n}\n\n.tooltip[x-placement^='top'] .tooltip-arrow {\n    border-width: 5px 5px 0 5px;\n    border-left-color: transparent !important;\n    border-right-color: transparent !important;\n    border-bottom-color: transparent !important;\n    bottom: -5px;\n    left: calc(50% - 4px);\n    margin-top: 0;\n    margin-bottom: 0;\n}\n\n.tooltip[x-placement^='bottom'] {\n    margin-top: 5px;\n}\n\n.tooltip[x-placement^='bottom'] .tooltip-arrow {\n    border-width: 0 5px 5px 5px;\n    border-left-color: transparent !important;\n    border-right-color: transparent !important;\n    border-top-color: transparent !important;\n    top: -5px;\n    left: calc(50% - 4px);\n    margin-top: 0;\n    margin-bottom: 0;\n}\n\n.tooltip[x-placement^='right'] {\n    margin-left: 5px;\n}\n\n.tooltip[x-placement^='right'] .tooltip-arrow {\n    border-width: 5px 5px 5px 0;\n    border-left-color: transparent !important;\n    border-top-color: transparent !important;\n    border-bottom-color: transparent !important;\n    left: -4px;\n    top: calc(50% - 5px);\n    margin-left: 0;\n    margin-right: 0;\n}\n\n.tooltip[x-placement^='left'] {\n    margin-right: 5px;\n}\n\n.tooltip[x-placement^='left'] .tooltip-arrow {\n    border-width: 5px 0 5px 5px;\n    border-top-color: transparent !important;\n    border-right-color: transparent !important;\n    border-bottom-color: transparent !important;\n    right: -4px;\n    top: calc(50% - 5px);\n    margin-left: 0;\n    margin-right: 0;\n}\n\n.tooltip.popover .popover-inner {\n    background: #ffef93;\n    color: #555;\n    padding: 24px;\n    border-radius: 5px;\n    box-shadow: 0 5px 30px rgba(black, 0.1);\n}\n\n.tooltip.popover .popover-arrow {\n    border-color: #ffef93;\n}\n\n.tooltip[aria-hidden='true'] {\n    visibility: hidden;\n    opacity: 0;\n    transition: opacity 0.15s, visibility 0.15s;\n}\n\n.tooltip[aria-hidden='false'] {\n    visibility: visible;\n    opacity: 1;\n    transition: opacity 0.15s;\n}\n\n.template-title {\n    padding: 0 0 3px 5px;\n    display: inline-block;\n}\n\n.template-remove {\n    float: right;\n    margin-right: 4px;\n    margin-top: 2px;\n}\n\n.template-remove:hover {\n    animation-name: spin;\n    animation-duration: 500ms;\n    animation-timing-function: ease-out;\n}\n\n@keyframes spin {\n    from {\n        transform: rotate(0deg);\n    }\n\n    to {\n        transform: rotate(180deg);\n    }\n}\n\n.template-body {\n    display: flex;\n}\n</style>\n"],"sourceRoot":""}]);
