ight'][data-v-4f60cc3f] {\n    margin-left: 5px;\n}\n.tooltip[x-placement^='right'] .tooltip-arrow[data-v-4f60cc3f] {\n    border-width: 5px 5px 5px 0;\n    border-left-color: transparent !important;\n    border-top-color: transparent !important;\n    border-bottom-color: transparent !important;\n    left: -4px;\n    top: calc(50% - 5px);\n    margin-left: 0;\n    margin-right: 0;\n}\n.tooltip[x-placement^='left'][data-v-4f60cc3f] {\n    margin-right: 5px;\n}\n.tooltip[x-placement^='left'] .tooltip-arrow[data-v-4f60cc3f] {\n    border-width: 5px 0 5px 5px;\n    border-top-color: transparent !important;\n    border-right-color: transparent !important;\n    border-bottom-color: transparent !important;\n    right: -4px;\n    top: calc(50% - 5px);\n    margin-left: 0;\n    margin-right: 0;\n}\n.tooltip.popover .popover-inner[data-v-4f60cc3f] {\n    background: #ffef93;\n    color: #555;\n    padding: 24px;\n    border-radius: 5px;\n    box-shadow: 0 5px 30px rgba(black, 0.1);\n}\n.tooltip.popover .popover-arrow[data-v-4f60cc3f] {\n    border-color: #ffef93;\n}\n.tooltip[aria-hidden='true'][data-v-4f60cc3f] {\n    visibility: hidden;\n    opacity: 0;\n    transition: opacity 0.15s, visibility 0.15s;\n}\n.tooltip[aria-hidden='false'][data-v-4f60cc3f] {\n    visibility: visible;\n    opacity: 1;\n    transition: opacity 0.15s;\n}\n.vertical-align[data-v-4f60cc3f] {\n    display: flex;\n    align-items: center;\n}\n.vertical-align > p[data-v-4f60cc3f] {\n    margin: auto 10px;\n}\n.pattern > span[data-v-4f60cc3f] {\n    position: absolute;\n    top: 0;\n    left: 25px;\n    color: black;\n    background-color: grey;\n    padding: 1px 5px;\n    opacity: 0.8;\n    border-radius: 5px;\n}\n.pattern[data-v-4f60cc3f] {\n    position: relative;\n}\n", "",{"version":3,"sources":["webpack://./src/components/helpers/search-template-custom.vue"],"names":[],"mappings":";AA+TA;IACA,UAAA;IACA,QAAA;IACA,kBAAA;IACA,WAAA;IACA,YAAA;AACA;AAEA;IACA,cAAA;IACA,iBAAA;IACA,mBAAA;IACA,wBAAA;IACA,WAAA;IACA,gBAAA;AACA;AAEA;IACA,gBAAA;AACA;AAEA;IACA,yBAAA;AACA;AAEA;IACA,UAAA;AACA;AAEA;IACA,yBAAA;IACA,cAAA;AACA;AAEA;IACA,mBAAA;IACA,WAAA;IACA,mBAAA;IACA,qBAAA;IACA,yBAAA;IACA,uDAAA;IACA,oDAAA;IACA,+CAAA;AACA;AAEA;IACA,QAAA;IACA,SAAA;IACA,kBAAA;IACA,WAAA;IACA,yBAAA;IACA,UAAA;AACA;AAEA;IACA,kBAAA;AACA;AAEA;IACA,2BAAA;IACA,yCAAA;IACA,0CAAA;IACA,2CAAA;IACA,YAAA;IACA,qBAAA;IACA,aAAA;IACA,gBAAA;AACA;AAEA;IACA,eAAA;AACA;AAEA;IACA,2BAAA;IACA,yCAAA;IACA,0CAAA;IACA,wCAAA;IACA,SAAA;IACA,qBAAA;IACA,aAAA;IACA,gBAAA;AACA;AAEA;IACA,gBAAA;AACA;AAEA;IACA,2BAAA;IACA,yCAAA;IACA,wCAAA;IACA,2CAAA;IACA,UAAA;IACA,oBAAA;IACA,cAAA;IACA,eAAA;AACA;AAEA;IACA,iBAAA;AACA;AAEA;IACA,2BAAA;IACA,wCAAA;IACA,0CAAA;IACA,2CAAA;IACA,WAAA;IACA,oBAAA;IACA,cAAA;IACA,eAAA;AACA;AAEA;IACA,mBAAA;IACA,WAAA;IACA,aAAA;IACA,kBAAA;IACA,uCAAA;AACA;AAEA;IACA,qBAAA;AACA;AAEA;IACA,kBAAA;IACA,UAAA;IACA,2CAAA;AACA;AAEA;IACA,mBAAA;IACA,UAAA;IACA,yBAAA;AACA;AAEA;IACA,aAAA;IACA,mBAAA;AACA;AAEA;IACA,iBAAA;AACA;AAEA;IACA,kBAAA;IACA,MAAA;IACA,UAAA;IACA,YAAA;IACA,sBAAA;IACA,gBAAA;IACA,YAAA;IACA,kBAAA;AACA;AAEA;IACA,kBAAA;AACA","sourcesContent":["<template>\n    <div id=\"search-template-custom\" class=\"form-group\">\n        <!-- Display add a new tempalte input -->\n        <div class=\"row\">\n            <div class=\"col-sm-2\">\n                <label for=\"default_templates\" class=\"control-label\">\n                    <span>Add Custom Template</span>\n                </label>\n            </div>\n        </div>\n        <div class=\"row\">\n            <div class=\"col-lg-12 content\">\n                <div class=\"row\">\n                    <div class=\"col-sm-2\">\n                        <label class=\"control-label\">\n                            <span>Title:</span>\n                        </label>\n                    </div>\n                    <div class=\"col-sm-10 content\">\n                        <select\n                            id=\"default_page\"\n                            name=\"default_page\"\n                            v-model=\"selectedTitle\"\n                            class=\"form-control input-sm\"\n                        >\n                            <option\n                                :value=\"option\"\n                                v-for=\"option in selectTitles\"\n                                :key=\"option.title\"\n                            >{{ titleOptionDescription(option) }}\n                            </option>\n                        </select>\n                    </div>\n                </div>\n\n                <div class=\"row\">\n                    <div class=\"col-sm-2\">\n                        <label class=\"control-label\">\n                            <span>Episode or Season search:</span>\n                        </label>\n                    </div>\n                    <div class=\"col-sm-10\">\n                        <div class=\"checkbox\">\n                            <label for=\"episode\">Episode</label>\n                            <input\n                                type=\"radio\"\n                                id=\"episode\"\n                                :value=\"'episode'\"\n                                v-model=\"episodeOrSeason\"\n                            >\n                            <label for=\"episode\">Season</label>\n                            <input\n                                type=\"radio\"\n                                id=\"season\"\n                                :value=\"'season'\"\n                                v-model=\"episodeOrSeason\"\n                            >\n\n                        </div>\n                    </div>\n                </div>\n\n                <div class=\"row\">\n                    <div class=\"col-sm-2\">\n                        <label class=\"control-label\">\n                            <span>Search Template:</span>\n                        </label>\n                    </div>\n                    <div class=\"col-sm-10 pattern\">\n                        <span ref=\"inputTitle\" v-if=\"selectedTitle\">%SN</span>\n                        <input\n                            type=\"text\"\n                            name=\"new_pattern\"\n                            v-model=\"addPattern\"\n                            class=\"form-control-inline-max input-sm max-input350 search-pattern\"\n                            style=\"padding-left: 50px\"\n                            :disabled=\"!selectedTitle\"\n                        >\n                        <input type=\"checkbox\" v-model=\"enabled\">\n                        <p v-if=\"!validated && isValidMessage\">{{isValidMessage}}</p>\n                    </div>\n                </div>\n\n                <div class=\"row\">\n                    <div class=\"col-sm-2\">\n                        <label class=\"control-label\">\n                            <span>example:</span>\n                        </label>\n                    </div>\n                    <div class=\"col-sm-10\" :class=\"{ 'error-message': !validated }\">\n                        {{ patternExample }}\n                    </div>\n                </div>\n\n                <div class=\"row\">\n                    <div class=\"col-sm-2\" />\n                    <div class=\"col-sm-10 vertical-align\">\n                        <input\n                            id=\"submit\"\n                            type=\"submit\"\n                            value=\"Add custom exception\"\n                            class=\"btn-medusa pull-left button\"\n                            :disabled=\"!validated\"\n                            @click=\"add\"\n                        >\n                        <p>{{ notification }}</p>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport { mapState } from 'vuex';\nimport { VTooltip } from 'v-tooltip';\nimport AsyncComputed from 'vue-async-computed';\nimport debounce from 'lodash/debounce';\n\nexport default {\n    name: 'search-template-custom',\n    directives: {\n        tooltip: VTooltip\n    },\n    plugins: {\n        AsyncComputed\n    },\n    props: {\n        /**\n         * Provide the custom naming type. -Like sports, anime, air by date- description.\n         * If none provided we asume this is the default episode naming component.\n         * And that means there will be no checkbox available to enable/disable it.\n         */\n        format: {\n            type: String,\n            default: ''\n        },\n        show: {\n            type: Object,\n            default: null\n        },\n        animeType: {\n            type: Number,\n            default: 3\n        }\n    },\n    data() {\n        return {\n            showFormat: null,\n            patternExample: '',\n            validated: false,\n            isValidMessage: '',\n            seasonPattern: false,\n            showExample: false,\n\n            selectedTitle: null,\n            addPattern: '',\n            enabled: true,\n            separator: ' ',\n            episodeOrSeason: 'episode',\n            notification: ''\n        };\n    },\n    watch: {\n        addPattern(newPattern) {\n            if (!newPattern) {\n                return;\n            }\n            this.notification =\n                'testing the pattern as soon as you stop typing';\n            this.debouncedIsValid();\n            this.debouncedTestNaming();\n        }\n    },\n    created() {\n        this.debouncedIsValid = debounce(this.isValid, 500);\n        this.debouncedTestNaming = debounce(this.testNaming, 500);\n    },\n    mounted() {\n        const { show } = this;\n        const { title } = show;\n\n        this.selectedTitle = {\n            indexer: show.indexer,\n            seriesId: show.id[show.indexer],\n            season: -1,\n            title\n        };\n    },\n    computed: {\n        ...mapState({\n            client: state => state.auth.client\n        }),\n        selectTitles() {\n            const { show } = this;\n            const { config, title } = show;\n            const { aliases } = config;\n\n            const titleOption = {\n                indexer: show.indexer,\n                seriesId: show.id[show.indexer],\n                season: -1,\n                title\n            };\n\n            return [...[titleOption], ...aliases];\n        },\n        templateExists() {\n            const { addPattern, selectedTitle, show } = this;\n            const { config } = show;\n            const combinedPattern = `%SN${addPattern}`;\n            return config.searchTemplates.find(\n                template => template.title === selectedTitle.title &&\n                template.season === selectedTitle.season &&\n                template.template === combinedPattern\n            );\n        }\n    },\n    methods: {\n        titleOptionDescription(option) {\n            let seasonDescription = '';\n\n            if (option.season > -1) {\n                seasonDescription = ` (Season ${option.season} exception)`;\n            }\n\n            return option.title + seasonDescription;\n        },\n        async testNaming() {\n            const { animeType, addPattern, format, selectedTitle } = this;\n            if (!addPattern) {\n                return;\n            }\n\n            console.debug(`Test pattern ${addPattern}`);\n\n            const combinedPattern = `${selectedTitle.title}${addPattern}`;\n\n            let params = {\n                pattern: combinedPattern\n            };\n\n            const formatMap = new Map([\n                // eslint-disable-next-line camelcase\n                ['anime', { anime_type: animeType }],\n                ['sports', { sports: true }],\n                ['airByDate', { abd: true }]\n            ]);\n\n            if (format !== '') {\n                params = { ...params, ...formatMap.get(format) };\n            }\n\n            let response = '';\n\n            try {\n                response = await this.client.apiRoute.get(\n                    'config/postProcessing/testNaming',\n                    {\n                        params,\n                        timeout: 20000\n                    }\n                );\n                this.patternExample = response.data;\n            } catch (error) {\n                console.warn(error);\n            }\n        },\n        isValid() {\n            const { addPattern } = this;\n            if (!addPattern) {\n                return;\n            }\n\n            if (!(addPattern.startsWith(' ') || addPattern.startsWith('.'))) {\n                this.validated = false;\n                this.isValidMessage = 'Dont forget to start the pattern with a separator. For example a dot or space.';\n                return;\n            }\n\n            if (this.templateExists) {\n                this.validated = false;\n                this.isValidMessage = 'This template combination is already in use';\n                return;\n            }\n\n            this.validated = true;\n            this.isValidMessage = '';\n        },\n        add() {\n            const {\n                show,\n                addPattern,\n                episodeOrSeason,\n                enabled,\n                selectedTitle\n            } = this;\n            const { title } = show;\n\n            this.$emit('input', {\n                pattern: `%SN${addPattern}`,\n                seasonSearch: episodeOrSeason === 'season',\n                enabled,\n                title: selectedTitle\n            });\n\n            this.selectedTitle = {\n                indexer: show.indexer,\n                seriesId: show.id[show.indexer],\n                season: -1,\n                title\n            };\n            this.addPattern = '';\n            this.episodeOrSeason = 'episode';\n        }\n    }\n};\n</script>\n<style scoped>\n.show-template {\n    left: -8px;\n    top: 4px;\n    position: absolute;\n    z-index: 10;\n    opacity: 0.6;\n}\n\n.template-example {\n    display: block;\n    line-height: 20px;\n    margin-bottom: 15px;\n    padding: 2px 5px 2px 2px;\n    clear: left;\n    max-width: 338px;\n}\n\n.tooltip-wrapper {\n    min-width: 340px;\n}\n\n.invalid {\n    background-color: #ff5b5b;\n}\n\n.error-message {\n    color: red;\n}\n\n.tooltip {\n    display: block !important;\n    z-index: 10000;\n}\n\n.tooltip .tooltip-inner {\n    background: #ffef93;\n    color: #555;\n    border-radius: 16px;\n    padding: 5px 10px 4px;\n    border: 1px solid #f1d031;\n    -webkit-box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\n    -moz-box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\n    box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.15);\n}\n\n.tooltip .tooltip-arrow {\n    width: 0;\n    height: 0;\n    position: absolute;\n    margin: 5px;\n    border: 1px solid #ffef93;\n    z-index: 1;\n}\n\n.tooltip[x-placement^='top'] {\n    margin-bottom: 5px;\n}\n\n.tooltip[x-placement^='top'] .tooltip-arrow {\n    border-width: 5px 5px 0 5px;\n    border-left-color: transparent !important;\n    border-right-color: transparent !important;\n    border-bottom-color: transparent !important;\n    bottom: -5px;\n    left: calc(50% - 4px);\n    margin-top: 0;\n    margin-bottom: 0;\n}\n\n.tooltip[x-placement^='bottom'] {\n    margin-top: 5px;\n}\n\n.tooltip[x-placement^='bottom'] .tooltip-arrow {\n    border-width: 0 5px 5px 5px;\n    border-left-color: transparent !important;\n    border-right-color: transparent !important;\n    border-top-color: transparent !important;\n    top: -5px;\n    left: calc(50% - 4px);\n    margin-top: 0;\n    margin-bottom: 0;\n}\n\n.tooltip[x-placement^='right'] {\n    margin-left: 5px;\n}\n\n.tooltip[x-placement^='right'] .tooltip-arrow {\n    border-width: 5px 5px 5px 0;\n    border-left-color: transparent !important;\n    border-top-color: transparent !important;\n    border-bottom-color: transparent !important;\n    left: -4px;\n    top: calc(50% - 5px);\n    margin-left: 0;\n    margin-right: 0;\n}\n\n.tooltip[x-placement^='left'] {\n    margin-right: 5px;\n}\n\n.tooltip[x-placement^='left'] .tooltip-arrow {\n    border-width: 5px 0 5px 5px;\n    border-top-color: transparent !important;\n    border-right-color: transparent !important;\n    border-bottom-color: transparent !important;\n    right: -4px;\n    top: calc(50% - 5px);\n    margin-left: 0;\n    margin-right: 0;\n}\n\n.tooltip.popover .popover-inner {\n    background: #ffef93;\n    color: #555;\n    padding: 24px;\n    border-radius: 5px;\n    box-shadow: 0 5px 30px rgba(black, 0.1);\n}\n\n.tooltip.popover .popover-arrow {\n    border-color: #ffef93;\n}\n\n.tooltip[aria-hidden='true'] {\n    visibility: hidden;\n    opacity: 0;\n    transition: opacity 0.15s, visibility 0.15s;\n}\n\n.tooltip[aria-hidden='false'] {\n    visibility: visible;\n    opacity: 1;\n    transition: opacity 0.15s;\n}\n\n.vertical-align {\n    display: flex;\n    align-items: center;\n}\n\n.vertical-align > p {\n    margin: auto 10px;\n}\n\n.pattern > span {\n    position: absolute;\n    top: 0;\n    left: 25px;\n    color: black;\n    background-color: grey;\n    padding: 1px 5px;\n    opacity: 0.8;\n    border-radius: 5px;\n}\n\n.pattern {\n    position: relative;\n}\n</style>\n"],"sourceRoot":""}]);
